import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../pages/Home.vue";
import AddEcard from "../pages/AddEcard.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/addecard",
    name: "addecard",
    component: AddEcard
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
